<template>
  <div>
    <!--轮播图-->
    <van-swipe :autoplay="2000" indicator-color="#282828">
      <van-swipe-item v-for="(item, index) in swipeImages" :key="index">
        <van-image fit="contain" width="100%" height="10.714rem" :src="item.picture" />
      </van-swipe-item>
    </van-swipe>

    <!--导航栏-->
    <van-row type="flex" justify="space-around" class="nav-box">
      <van-col
        v-for="(item, inx) of navList"
        class="tx-center dis-flex flex-column align-item-center"
        :key="inx"
        :span="5"
        @click="goto(item.route)"
      >
        <div class="nav-icon-box"></div>
        <div class="color-282828 font-12">{{item.title}}</div>
      </van-col>
    </van-row>
    <p class="divider" />

    <!--滚动公告-->
    <div class="notice-box">
      <div class="notice-title">
        滚动
        <br />公告
      </div>

      <div class="color-656565 font-10">
        <div>
          <span class="badge">当前活动</span>
          <van-swipe
            style="height: 20px;"
            :vertical="true"
            autoplay="5000"
            :show-indicators="false"
          >
            <van-swipe-item
              style="width:200px"
              v-for="(item,index) in notice"
              :key="index"
            >{{item.title}}</van-swipe-item>
          </van-swipe>
        </div>
        <div style="margin-top: 0.5rem;">
          <span class="badge">牦牛动态提醒</span>
          <van-swipe
            style="height: 20px;"
            :vertical="true"
            autoplay="5000"
            :show-indicators="false"
          >
            <van-swipe-item
              style="width:200px"
              v-for="(item,index) in task"
              :key="index"
            >{{item.title}}</van-swipe-item>
          </van-swipe>
        </div>
      </div>
    </div>
    <p class="divider" />

    <!--牧场管理-->

    <p class="title color-656565 font-12">我管理的牦牛（小金屠牧场）</p>
    <div class="dis-flex size flex-column">
      <filter-nav-control
        :filter-nav-list="ButtonList"
        v-model="activeNav"
        @change="adoptTypeChange"
      />

      <div class="flex-1" style="overflow-y: auto">
        <!--牦牛认养 Item-->
        <yak-adopt-item
          v-if="update"
          :is-show-adopt-bt="false"
          :activeNavChange="activeNav"
          @detail="adoptDetail"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { banner_task } from "../../api/api";
import YakAdoptItem from "../../components/yak-adopt/yak-adopt-item";
import FilterNavControl from "../../components/common/filter-nav-control";
export default {
  name: "nomads-home",
  components: { FilterNavControl, YakAdoptItem },
  data() {
    return {
      update: true,
      swipeImages: [],
      navList: [
        { title: "商城", route: "http://www.baidu.com" },
        { title: "商城", route: "http://www.baidu.com" },
        { title: "商城", route: "http://www.baidu.com" },
        { title: "商城", route: "http://www.baidu.com" }
      ],
      ButtonList: [
        { title: "管理中的耗牛", key: "stay" },
        { title: "管理完毕的耗牛", key: "completion" }
      ],
      activeNav: "stay",
      notice: "",
      task: ""
    };
  },
  methods: {
    /**
     * @description: 认养类型改变
     * @date 2020/2/12
     */
    goto(src) {
      window.location.href = src;
    },
    adoptTypeChange(event) {
      console.log(event);
      this.update = false;
      // 在组件移除后，重新渲染组件
      // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
      this.$nextTick(() => {
        this.update = true;
      });
      console.log(this.activeNav);
    },
    /**
     * @description: 牦牛详情
     * @date 2020/2/12
     */
    adoptDetail(event) {
      console.log(event)
      this.$router.push({
        path: "/nomads-home/nomads-detail",
        query: {
          id: event.id,
          activeNav: this.activeNav
        }
      });
    }
  },
  created() {
    banner_task().then(res => {
      console.log(res.data.data);
      this.swipeImages = res.data.data.banner;
      this.notice = res.data.data.notice;
      this.task = res.data.data.task;
    });
  }
};
</script>

<style scoped lang="less">
/*分割线*/
.divider {
  height: 0.214rem;
}

/deep/ .van-swipe__indicator {
  background-color: #989898;
}

/*滚动公告*/
.nav-box {
  margin-top: 1.286rem;
  padding-bottom: 1.5rem;

  /*导航栏图标容器*/

  .nav-icon-box {
    width: 2.857rem;
    height: 2.857rem;
    background: rgba(254, 165, 3, 1);
    border-radius: 50%;
  }
}

/*公告容器*/
.notice-box {
  display: flex;
  align-items: center;
  padding: 1.143rem 1.071rem;

  /*标题*/

  .notice-title {
    font-size: 0.786rem;
    font-weight: bold;
    color: rgba(251, 110, 110, 1);
    margin-right: 1.071rem;
  }
}

/*卡片盒子*/
.card-box {
  padding: 0.643rem 0.429rem 0.643rem 0.929rem;

  /*卡片图片*/

  .card-img-box {
    margin-top: 1.429rem;
    padding-left: 0.643rem;
  }

  /*卡片主内容*/
  .card-content {
    margin-left: 1.714rem;
  }
}

/*预约按钮*/
.subscribe-bt {
  flex-shrink: 0;
  font-size: 0.857rem;
  font-weight: bold;
  color: rgba(254, 165, 3, 1);
  margin-left: 1.429rem;
  margin-right: 1.643rem;
  background: none;
  border: none;
}

.title {
  padding: 0.9375rem 0 1rem 0.75rem;
}
</style>
