<template>
  <div>
    <div
      class="dis-flex align-item-center yak-adopt-item-box"
      v-for="(item,index) of ysklist"
      :key="index"
    >
      <van-image
        :src="item.yaks_img"
        fit="cover"
        @click="$emit('detail', {id:item.id,sex:item.sex})"
        class="yak-image"
      />
      <div class="flex-1 font-9" style="padding: 0.786rem 0 0.429rem;">
        <div class="font-12">
          {{item.yaks_name}}
          <span
            class="icon-cow"
            v-if="item.yaks_sex=='母'"
            style="margin-left: 0.5rem;"
          ></span>
          <span class="icon-bull" v-else style="margin-left: 0.5rem;"></span>
        </div>
        <div class="color-656565 mg-top-10">耳标号：{{item.yaks_tag}}</div>
        <div class="mg-top-10">
          <span class="badge">{{item.age}}岁</span>
        </div>
        <div class="color-F7CA3B mg-top-10">
          <img
            src="../../assets/images/yak-adopt/position-yellow.png"
            class="icon-img"
            style="width: 0.857rem;height: 0.929rem;margin-right: 0.429rem;"
          />
          {{item.adopt.pasture_name}}
        </div>
      </div>
      <button class="adopt-bt button" v-if="isShowAdoptBt" @click="$emit('adopt', {id:item.id})">认养</button>
    </div>
  </div>
</template>

<script>
import { yak_adoptlist, herdsmanHome, myyak } from "../../api/api";
export default {
  name: "yak-adopt-item",
  data() {
    return {
      ysklist: [],
      invest_type: ""
    };
  },
  props: {
    // 展示认养按钮
    isShowAdoptBt: {
      default: true,
      type: Boolean
    },
    activeNavChange: String
  },
  methods: {
    yaKlist() {
      let data = {};
      if (this.activeNavChange == "edible") {
        data.invest_type = 1;
      } else if (this.activeNavChange == "invest") {
        data.invest_type = 0;
      } else if (this.activeNavChange == "stay") {
        data.is_manage = 1;
      } else if (this.activeNavChange == "completion") {
        data.is_manage = 2;
      } else if (this.activeNavChange == "myedible") {
        data.is_manage = 1;
      } else if (this.activeNavChange == "myinvest") {
        data.is_manage = 0;
      }
      if (
        this.activeNavChange == "edible" ||
        this.activeNavChange == "invest"
      ) {
        yak_adoptlist(data).then(res => {
          this.ysklist = res.data.data.data;
        });
      } else if (
        this.activeNavChange == "stay" ||
        this.activeNavChange == "completion"
      ) {
        console.log(this.activeNavChange);
        herdsmanHome(data).then(res => {
          this.ysklist = res.data.data;
          for (let i in this.ysklist) {
            this.ysklist[i].adopt = {
              pasture_name: this.ysklist[i].pasture_name
            };
          }
        });
      } else if (
        this.activeNavChange == "myedible" ||
        this.activeNavChange == "myinvest"
      ) {
        myyak(data).then(res => {
          console.log(res);
          this.ysklist = res.data.data.data;
           for (let i in this.ysklist) {
            this.ysklist[i].adopt = {
              pasture_name: this.ysklist[i].pasture_name
            };
          }
        });
      }
    }
  },
  created() {
    this.yaKlist();
  },
  updated() {}
};
</script>

<style scoped lang="less">
.badge {
  color: #989898;
  border-color: #979797;
  padding: 0 0.714rem;
}

/*牦牛认养Item 容器*/
.yak-adopt-item-box {
  padding: 1.429rem 2.571rem 1.071rem 0.714rem;
  border-bottom: 1px solid #f2f2f2;

  /*牦牛图片*/

  .yak-image {
    width: 9.286rem;
    height: 7.857rem;
    margin-right: 2.071rem;
  }

  /*认养按钮*/

  .adopt-bt {
    font-size: 1.071rem;
    font-weight: bold;
    color: #3399ff;
  }
}
</style>
